<template>
    <div id="edit-patient">
        <h3>Edit patient</h3>
        <div class="row center-align">
          <form @submit.prevent="updatePatient" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input disabled type="text" v-model="id" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="first_name" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="last_name" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="age" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="weight" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="height" required>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="job" required>
              </div>
            </div>
            <button type="submit" class="btn indigo">Submit</button>
            <router-link to='/' class="btn indigo">Cancel</router-link>
          </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'edit-patient',
  data () {
    return {
      id: null,
      first_name: null,
      last_name: null,
      age: null,
      weight: null,
      height: null,
      job: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('patients').where('id', '==', to.params.id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.id = doc.data().id
            vm.first_name = doc.data().first_name
            vm.last_name = doc.data().last_name
            vm.age = doc.data().age
            vm.height = doc.data().height
            vm.weight = doc.data().weight
            vm.job = doc.data().job
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('patients').where('id', '==', this.$route.params.id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.id = doc.data().id
            this.first_name = doc.data().first_name
            this.last_name = doc.data().last_name
            this.age = doc.data().age
            this.height = doc.data().height
            this.weight = doc.data().weight
            this.job = doc.data().job
          })
        })
    },
    updatePatient () {
      db.collection('patients').where('id', '==', this.$route.params.id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              id: this.id,
              first_name: this.first_name,
              last_name: this.last_name,
              age: this.age,
              weight: this.weight,
              height: this.height,
              job: this.job
            }).then(() => {
              this.$router.push({name: 'view-patient', params: {id: this.id}})
            })
          })
        })
    }
  }
}
</script>



