<template>
    <nav>
        <div class="nav-wrapper indigo col s12">
            <div class="container col s12">
                <ul class="left">
                    <li v-if="isLoggedIn"><span class="email white-text col s2 things">{{currentUser}}</span>
</li>
                </ul>
                <router-link to='/' class="brand-logo center col s5">Pharma Manager</router-link>
                <ul class="right">
                    <li v-if="!isLoggedIn" class="col s1"><router-link to="/login" class="things">Login</router-link></li>
                    <li v-if="!isLoggedIn" class="col s1"><router-link to="/register" class="things">Register</router-link></li>
                    <li v-if="isLoggedIn" class="col s1"><router-link to="/" class="things">List</router-link></li>
                    <li v-if="isLoggedIn" class="col s2"><button @click="logout" class="btn indigo lighten-3 btn-small things">Logout</button></li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'navbar',
  data () {
    return {
      isLoggedIn: false,
      currentUser: false
    }
  },
  created () {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true
      this.currentUser = firebase.auth().currentUser.email
    }
  },
  methods: {
    logout: function () {
      firebase.auth().signOut()
        .then(() => {
          this.$router.push('/login')
        })
    }
  }
}
</script>

<style scoped>
nav .brand-logo {
    font-size: 2.35vw;
}
.things {
  font-size: 86%
}
</style>



