<template>
    <div class="pagination-wrapper" v-if="totalPages() > 0">
        <span class="pagination-btn" v-if="showPreviousPage()" @click="updatePage(currentPage - 1)">
        &lt;
        </span>
        <span class = "pag-nr">Page {{currentPage + 1}} of {{totalPages()}}</span>
        <span class="pagination-btn" v-if="showNextPage()" @click="updatePage(currentPage + 1)">
        &gt;
        </span>
    </div>
</template>

<script>
export default {
  name: 'pagination',
  props: ['patients', 'currentPage', 'pageSize'],
  methods: {
    updatePage (pageNumber) {
      this.$emit('page:update', pageNumber)
    },
    totalPages () {
      return Math.ceil(this.patients.length / this.pageSize)
    },
    showPreviousPage () {
      return this.currentPage !== 0
    },
    showNextPage () {
      return this.currentPage !== (this.totalPages() - 1)
    }
  }
}
</script>

<style scoped>
.pagination-btn {
    cursor: pointer;
    font-size: 120%;
}
.pag-nr {
    font-size: 120%;
}
</style>



