<template>
    <div id="new-patient center-align">
        <h3>New patient</h3>
        <div class="row center-align">
          <form @submit.prevent="addPatient" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="first_name" required>
                <label>First Name</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="last_name" required>
                <label>Last Name</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="age" required>
                <label>Age</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="weight" required>
                <label>Weight (kg)</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="height" required>
                <label>Height (cm)</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="job" required>
                <label>Job</label>
              </div>
            </div>
            <button type="submit" class="btn indigo">Submit</button>
            <router-link to='/' class="btn indigo">Cancel</router-link>
          </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
import faker from 'faker'
export default {
  name: 'new-patient',
  data () {
    return {
      id: null,
      first_name: null,
      last_name: null,
      age: null,
      weight: null,
      height: null,
      job: null,
      drugs: []
    }
  },
  methods: {
    addPatient () {
      db.collection('patients').add({
        id: faker.random.number({min: 5000}),
        first_name: this.first_name,
        last_name: this.last_name,
        age: Number(this.age),
        weight: this.weight,
        height: this.height,
        job: this.job,
        drugs: this.drugs
      }).then(docRef => {
        this.$router.push('/')
      }).catch(err => console.log(err))
    }
  }
}
</script>



