        let age = faker.random.number({min: 18, max: 80})
        let height = faker.random.number({min: 160, max: 195})
        let weight = faker.random.number({min: 50, max: 100})
        let job = faker.name.jobTitle()
        let nr = faker.random.number({min: 1, max: 5})
        let drugs = []
        for (let i = 0; i < nr; i++) {
          drugs.push(listOfDrugs[faker.random.number(100)])
        }
        const drgs = drugs.map((obj) => { return Object.assign({}, obj) })
        let data = {
          id: i,
          first_name: firstname,
          last_name: lastName,
          age: age,
          height: height,
          weight: weight,
          job: job,
          drugs: drgs
        }

        db.collection('patients').add(data).then(console.log('Patient added!'))
      }
    },
    addRelationships: function () {
