<template>
<div class="container">
    <br>
      <div class="row">

          <div class="col s8 offset-s2 z-depth-5">
            <h4 id="title" class="center-align">Register</h4>
            <form>

            <div class="input-field" id="username">
              <input  type="text" class="validate" v-model="email">
              <label for="username">Email</label>
          </div>
          <div class="input-field" id="password">
            <input  type="password" class="validate" v-model="password">
            <label for="password">Password</label>
        </div>
        <button @click="register" class="waves-effect indigo btn btn-medium col s12" id="loginbtn">Register</button>
        <br>
        </form>
<br>
<br>
        </div>
      </div>

    </div>
</template>

<script>
import firebase from 'firebase'
export default {
  name: 'register',
  data: function () {
    return {
      email: '',
      password: ''
    }
  },
  methods: {
    register: function (e) {
      firebase.auth().createUserWithEmailAndPassword(this.email, this.password)
        .then(user => {
          alert('Account created!')
          this.$router.push('/')
        }, err => {
          alert(err.message)
        })
      e.preventDefault()
    }
  }
}
</script>



