<template>
    <div id="add-drug center-align">
        <h3>Add drug</h3>
        <div class="row center-align">
          <form @submit.prevent="addDrug" class="col s12">
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="drug_name" required>
                <label>Drug name</label>
              </div>
            </div>
            <div class="row">
              <div class="input-field col s12">
                <input type="text" v-model="drug_type" required>
                <label>Drug type</label>
              </div>
            </div>
            <button type="submit" class="btn indigo">Add</button>
            <router-link to='/' class="btn indigo">Cancel</router-link>
          </form>
        </div>
    </div>
</template>

<script>
import faker from 'faker'
import db from './firebaseInit'
export default {
  name: 'add-drug',
  data () {
    return {
      id: null,
      first_name: null,
      last_name: null,
      age: null,
      weight: null,
      height: null,
      job: null,
      drugs: [],
      drug_name: null,
      drug_type: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('patients').where('id', '==', to.params.id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.id = doc.data().id
            vm.first_name = doc.data().first_name
            vm.last_name = doc.data().last_name
            vm.age = doc.data().age
            vm.height = doc.data().height
            vm.weight = doc.data().weight
            vm.job = doc.data().job
            vm.drugs = doc.data().drugs
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('patients').where('id', '==', this.$route.params.id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.id = doc.data().id
            this.first_name = doc.data().first_name
            this.last_name = doc.data().last_name
            this.age = doc.data().age
            this.height = doc.data().height
            this.weight = doc.data().weight
            this.job = doc.data().job
            this.drugs = doc.data().drugs
          })
        })
    },
    addDrug () {
      this.drugs.push({drug_id: faker.random.number({min: 500}), drug_name: this.drug_name, drug_type: this.drug_type})
      db.collection('patients').where('id', '==', this.$route.params.id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.update({
              id: this.id,
              first_name: this.first_name,
              last_name: this.last_name,
              age: this.age,
              weight: this.weight,
              height: this.height,
              job: this.job,
              drugs: this.drugs
            }).then(() => {
              this.$router.push({name: 'view-patient', params: {id: this.id}})
            })
          })
        })
    }
  }
}
</script>



