<template>
    <div id="dashboard">
      <div class="center-align">
        <br>
        <button @click="sortedByName" class="btn indigo btn-small">Sort by name</button>
        <button @click="sortedByAgeAsc" class="btn indigo btn-small">Sort by age</button>
        <button @click="sortedByAgeDesc" class="btn indigo btn-small">Sort by age desc</button>
        </div>
        <br>
        <Pagination v-bind:patients="patients"
        v-on:page:update="updatePage"
        v-bind:currentPage="currentPage"
        v-bind:pageSize="pageSize"
        class="center-align">
        </Pagination>
        <ul class="collection with_header">
          <li class="collection-header">
            <h4 class="center-align col s6 m9 l12">Patients</h4>
          </li>
          <li v-for="patient in visiblePatients" v-bind:key="patient.id"
          class="center-align collection-item col s12">
          {{patient.first_name}} {{patient.last_name}} <div class="chip indigo lighten-3">{{patient.age}}</div>
          <router-link class="secondary-content"
           v-bind:to="{name:'view-patient', params:{id: patient.id}}">
           <div class="chip indigo lighten-3"><i class="fa fa-info" aria-hidden="true"></i></div>
           </router-link>
          </li>
        </ul>
        <div class="fixed-action-btn">
          <router-link to="/new" class="btn-floating btn-large pulse indigo darken-3">
          <i class="fa fa-address-card"></i>
          </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
import Pagination from './Pagination'
export default {
  name: 'dashboard',
  data () {
    return {
      patients: [],
      currentPage: 0,
      pageSize: 50,
      visiblePatients: []
    }
  },
  components: {
    Pagination
  },
  created () {
    db.collection('patients').orderBy('id').get().then(querySnaphot => {
      querySnaphot.forEach(doc => {
        const data = {
          'doc_id': doc.id,
          'id': doc.data().id,
          'first_name': doc.data().first_name,
          'last_name': doc.data().last_name,
          'age': doc.data().age,
          'height': doc.data().height,
          'weight': doc.data().weight,
          'job': doc.data().job
        }
        this.patients.push(data)
        this.updateVisiblePatients()
      })
    })
  },
  beforeMount: function () {
    this.updateVisiblePatients()
  },
  methods: {
    sortedByName () {
      this.patients = []
      db.collection('patients').orderBy('first_name').get().then(querySnaphot => {
        querySnaphot.forEach(doc => {
          const data = {
            'doc_id': doc.id,
            'id': doc.data().id,
            'first_name': doc.data().first_name,
            'last_name': doc.data().last_name,
            'age': doc.data().age,
            'height': doc.data().height,
            'weight': doc.data().weight,
            'job': doc.data().job
          }
          this.patients.push(data)
          this.updateVisiblePatients()
        })
      })
    },
    sortedByAgeDesc () {
      this.patients = []
      db.collection('patients').orderBy('age', 'desc').get().then(querySnaphot => {
        querySnaphot.forEach(doc => {
          const data = {
            'doc_id': doc.id,
            'id': doc.data().id,
            'first_name': doc.data().first_name,
            'last_name': doc.data().last_name,
            'age': doc.data().age,
            'height': doc.data().height,
            'weight': doc.data().weight,
            'job': doc.data().job
          }
          this.patients.push(data)
          this.updateVisiblePatients()
        })
      })
    },
    sortedByAgeAsc () {
      this.patients = []
      db.collection('patients').orderBy('age').get().then(querySnaphot => {
        querySnaphot.forEach(doc => {
          const data = {
            'doc_id': doc.id,
            'id': doc.data().id,
            'first_name': doc.data().first_name,
            'last_name': doc.data().last_name,
            'age': doc.data().age,
            'height': doc.data().height,
            'weight': doc.data().weight,
            'job': doc.data().job
          }
          this.patients.push(data)
          this.updateVisiblePatients()
        })
      })
    },
    updatePage (pageNumber) {
      this.currentPage = pageNumber
      this.updateVisiblePatients()
    },
    updateVisiblePatients () {
      this.visiblePatients = this.patients.slice(this.currentPage * this.pageSize, (this.currentPage * this.pageSize) + this.pageSize)
      if (this.visiblePatients.length === 0 && this.currentPage > 0) {
        this.updatePage(this.currentPage - 1)
      }
    }
  }
}
</script>



