<template>
    <div id="view-patient">
        <ul class="collection with-header center-align">
          <li class="collection-header">
            <h4>{{first_name}} {{last_name}}</h4>
          </li>
          <li class="collection-item">Age: {{age}} years old</li>
          <li class="collection-item">Weight: {{weight}} kg</li>
          <li class="collection-item">Height: {{height}} cm</li>
          <li class="collection-item">Job: {{job}}</li>
          <li class="collection-header">Drugs:</li>
          <li v-for="(drug, index) in drugs" v-bind:key="drug.drug_id"
          class="center-align collection-item">
          {{drug.drug_name}} <div class="chip indigo lighten-3">{{drug.drug_type}}</div>
          <div class="chip indigo lighten-3 right-align" @click="deleteDrug(index)"><i class="fa fa-minus-circle" aria-hidden="true" ></i></div>
          </li>
        </ul>
        <div class="center-align"><button @click="deletePatient" class="btn indigo">Delete</button></div>
        <br>
        <div class="fixed-action-btn">
           <router-link v-bind:to="{name:'add-drug', params:{
            id: id
          }}" class="btn-floating btn-large pulse indigo darken-3">
          <i class="fa fa-plus"></i>
          </router-link>
          <router-link v-bind:to="{name:'edit-patient', params:{
            id: id
          }}" class="btn-floating btn-large pulse indigo darken-3">
          <i class="fa fa-pencil"></i>
          </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-patient',
  data () {
    return {
      id: null,
      first_name: null,
      last_name: null,
      age: null,
      job: null,
      height: null,
      weight: null,
      drugs: []
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('patients').where('id', '==', to.params.id).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          next(vm => {
            vm.id = doc.data().id
            vm.first_name = doc.data().first_name
            vm.last_name = doc.data().last_name
            vm.age = doc.data().age
            vm.height = doc.data().height
            vm.weight = doc.data().weight
            vm.job = doc.data().job
            vm.drugs = doc.data().drugs
          })
        })
      })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('patients').where('id', '==', this.$route.params.id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            this.id = doc.data().id
            this.first_name = doc.data().first_name
            this.last_name = doc.data().last_name
            this.age = doc.data().age
            this.height = doc.data().height
            this.weight = doc.data().weight
            this.job = doc.data().job
            this.drugs = doc.data().drugs
          })
        })
    },
    deletePatient () {
      if (confirm('Do you want to delete the patient?')) {
        db.collection('patients').where('id', '==', this.$route.params.id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
      }
    },
    deleteDrug (index) {
      if (confirm('Do you want to delete this drug?')) {
        this.drugs.splice(index, 1)
        db.collection('patients').where('id', '==', this.$route.params.id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.update({
                id: this.id,
                first_name: this.first_name,
                last_name: this.last_name,
                age: this.age,
                weight: this.weight,
                height: this.height,
                job: this.job,
                drugs: this.drugs
              }).then(() => {
                // eslint-disable-next-line handle-callback-err
                this.$router.push({name: 'view-patient', params: {id: this.id}}).catch(err => {})
              })
            })
          })
      }
    },
    showDrugs () {
      // db.collection('relationships').where('patient_id', '==', this.$route.params.id).get()
      //   .then(querySnapshot => {
      //     querySnapshot.forEach(doc => {
      //       this.drugs_id.push(doc.data().drug_id)
      //     })
      //   })
      // for (let i = 0; i < this.drugs_id.lenght; i++) {
      //   db.collection('drugs').where('drug_id', '==', this.drugs_id[i]).get()
      //     .then(querySnapshot => {
      //       querySnapshot.forEach(doc => {
      //         let data = {
      //           'drug_id': doc.data().drug_id,
      //           'drug_name': doc.data().drug_name,
      //           'drug_type': doc.data().drug_type
      //         }
      //         this.drugs.push(data)
      //       })
      //     })
      // }
      // console.log(this.drugs[1])
    }
  }
}
</script>



